@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,900&display=swap');
:root {
	--base-1: #EEE841;
	--green: #53C72B
  }
  
html
{
	padding:0px;
	margin:0px;
}
body
{
	padding:0px;
	margin:0px;
	font-family: 'Roboto', sans-serif !important; 
	background-color: #E8EAEF !important;
}
a
{
	color: #000 !important;
}
.profile-section
{
	display: flex;
	flex-direction: column;
	align-items: center;
}
.profile-section .profile-username
{
	font-size: 20px;
	font-weight: 400;
}
.profile-section .profile-name
{
	font-size: 50px;
	font-weight: bold;
}
.profile-section .profile-image img
{
	width:200px;
	height:200px;
	border-radius: 50%;
}
.profile-section .profile-image 
{
	display: flex;
	margin-bottom: 20px;
}
.profile-section .profile-bg
{
	width:200px;
	height:200px;
	background-color: var(--base-1);
	border-radius: 50%;
	margin-left: -130px;
	z-index: -1;
}
.nav 
{
	padding: 20px 0px;
}
.nav .nav-logo
{
	font-weight: bolder;
	font-size: 20px;
	border: 2px solid var(--base-1);
	height:70px;
	width:70px;
	border-radius: 50%;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}
.nav-right 
{
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}
.nav-right .nav-btn
{
	background-color: var(--base-1);
	padding: 10px 20px;
	font-weight: 600;
	letter-spacing: 2px;
}

.profile-hire-status::before
{
	background-color:var(--green);
	content:'';
	display: inline-block;
	height:10px;
	width:10px;
	border-radius: 50px;
	margin-right: 10px;
}
.profile-location
{
	display: flex;
	margin-bottom: 10px;
}
.profile-location .profile-location-icon
{
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px;
	background-color: black;
	margin-right: 4px;
	color: var(--base-1);
}
.profile-location .profile-location-text
{
	background-color: black;
	color: #E8EAEF;
	padding: 10px 20px;
	font-weight:bold;
	display: flex;
	align-items: center;
	justify-content: center;
}
.about-section
{
	padding-left:50px;
}
.bl
{
	border-left: 1px solid grey;
}
.about-section .about-profile h1
{
	font-weight: bold;
	font-size: 32px;
}
.about-section .about-profile h2
{
	font-weight: bold;
	font-size: 18px;
}
.about-profile 
{
	margin-bottom: 40px;
}
.about-profile span
{
	background-color: var(--base-1);
	margin-top:10px;
	margin-right:10px;
	padding: 10px;
	display: inline-block;
}
.project-item
{
	background-color: #fff;
	box-shadow: 10;
	filter: drop-shadow(0px 9px 7px rgba(0, 0, 0, 0.25));
	padding: 20px;
	margin-bottom: 20px;
}
.project-item .project-title
{
	font-weight: bold;
	font-size: 20px;
	margin-bottom: 10px;
}
.project-item .project-link
{
	opacity: 0.8;
	margin-bottom: 10px;
}
.project-item .project-desc
{
	margin-bottom: 10px;
	word-wrap: break-word;
} 
.project-item .project-tags span
{
	display: inline-block;
	background-color: #E8EAEF;
	font-weight:bold;
	padding: 10px 15px;
	font-size:14px;
	border-radius: 20px;
}
span.ico
{
	background-color: transparent;
	font-size: 40px;
	opacity: 0.5;
}
.skills
{
	list-style: none;
	padding: 0px;
	display: flex;
	flex-wrap:wrap;
}
.skills li
{
	margin:10px;
	border:2px solid #000;
	border-radius: 50%;
	height:100px !important;
	width:100px !important;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
	font-size: 14px;
}
.skills li img
{
	height: 80px;
	position: absolute;
	opacity: 0.09;
	border-radius: 50px;
}
.filter-btn
{
	list-style: none;
	padding: 0px;
	display: flex;
	flex-wrap:wrap;
}
.filter-btn li
{
	border:2px solid grey;
	padding: 10px 20px;
	border-radius: 20px;
	font-size: 12px;
}
.filter-text
{
	border:0px solid transparent !important;
	display: flex;
	align-items: center;
	font-size:18px !important;
	color: grey !important;
}
.filter-btn .btn
{
	background-color:transparent ;
	border:0px solid transparent
}
.profile-not-found
{
	height:100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size:100px;
	font-weight: bolder;
	opacity: 0.5;
	flex-direction: column;
}
@media all and (max-width: 480px) { 
	.project-link
	{
		display: none;
	}
	.about-section
	{
		margin: 30px 0px;
		padding: 0px !important;
	}
	.bl
	{
		border:0px solid transparent
	}
	.bl-sm
	{
		border-left: 1px solid grey;
	}
	.abouts-stats
	{
		text-align: center;
	}
	.skills li
	{
		width:80px !important;
		height:80px !important
	}
	.filter-text
	{
		display:block;
		width:100%;
	}
	.filter-btn .btn 
	{
		padding: 5px 10px !important;
	}
	.nav-btn
	{
		margin-top: 20px;
		border-radius: 50%;
		height:50px;
		width:50px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.nav-btn span
	{
		display: none;
	}
}
